body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.Intro {
  height: calc(100vh - 78px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
}

.Intro h1 {
  text-align: center;
  font-size: 75px;
  font-weight: 650;
  margin-top: -10%;
}

.Intro p {
  width: 75%;
  text-align: center;
  font-size: 30px;
  font-weight: 450;
  margin-top: 0px;
  overflow-wrap: normal;
}

.Skills {
  background-color: white;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #282c34;
}

.Skills h1 {
  font-size: 60px;
  font-weight: 600;
  margin-top: 140px;
  margin-bottom: -15px;
}

.Skills h2 {
  font-size: 45px;
  font-weight: 500;
  padding: 30px;
  margin-bottom: -40px;
}

.Skills p {
  font-size: 30px;
  font-weight: 450;
  padding: 30px;
  margin-top: 0px;
}  
.Projects {
    height: calc(100vh - 78px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
  }

  .Projects h1 {
    text-align: center;
    font-size: 75px;
    font-weight: 650;
    margin-top: -10%;
  }
.Experience {
    height: calc(100vh - 78px);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
  }

  .Experience h1 {
    text-align: center;
    font-size: 75px;
    font-weight: 650;
    margin-top: -10%;
  }
.NavBar {
  width: 100%;
  height: 80px;
  font-size: calc(10px + 4vmin);
}

.Headers {
  display: flex;
  justify-content: center;
  align-items: center;
  grid-column-gap: 8%;
  -webkit-column-gap: 8%;
          column-gap: 8%;
}

.Headers a {
  text-decoration: none;
  color: white;
  font-weight: 500;
  margin-top: 20px;
  transition: color 0.2s ease-out, 0.2s;
}

.Headers a:hover {
  color: #E4E3E3;
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.Footer {
  width: 100%;
  height: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Links svg {
  color: white;
  margin: 20px;
  margin-top: 0px;
  margin-bottom: 10px;
  font-size: 90px;
  cursor: pointer;
  transition: color 0.3s ease-out, 0.3s;
}

.Links svg:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  color: #E4E3E3;
}

@media only screen and (max-width: 800px) {
  .footer svg {
    font-size: 70px;
  }

  .footer {
    font-size: 20px;
    min-height: 150px;
    margin-top: 150px;
  }
}

@media only screen and (max-width: 600px) {
  .footer svg {
    font-size: 50px;
  }

  .footer {
    font-size: 15px;
  }
}
.App {
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.Main {
    width: 100%;
    min-height: 70vh;
    align-items: center;
}
